import { useTranslation } from "react-i18next";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { useState } from "react";
import InstituteStudent from "../../InstituteStudent/InstituteStudent";
import { useStudentLeaving } from "../../../../hooks/institute/institute-staff-student-api";
import QvipleLoader from "../../../Loader/QvipleLoader";

const CertificateIssueMenu = ({ onClose, instituteId }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");
  const [state, setState] = useState("");
  const [studentLeaving] = useStudentLeaving();

  const onCloseStep = () => {
    setStep("");
  };

  const onNavigate = (type, val) => {
    if (instituteId && val?.studentGRNO) {
      setDisabled((pre) => !pre);
      studentLeaving({
        id: instituteId,
        gr: val?.studentGRNO,
        leavingFeedback: {
          number: val?.studentGRNO,
          studentCertificateNo: "",
          leaving_date: "",
          bookNo: "",
          studentUidaiNumber: "",
          studentPreviousSchool: "",
          studentLeavingBehaviour: "",
          studentLeavingStudy: "",
          studentLeavingReason: "",
          studentRemark: "",
          instituteJoinDate: "",
          instituteLeavingDate: "",
          leaving_degree: "",
          leaving_since_date: "",
          leaving_course_duration: "",
          elective_subject_one: "",
          elective_subject_second: "",
          leaving_project_work: "",
          leaving_guide_name: "",
        },
      })
        .then((res) => {
          setDisabled((pre) => !pre);
          if (type === "LEAVING") {
            navigate(`/q/${params.username}/student/certificate/form`, {
              state: {
                student: res.data.student,
                download: res.data.download,
                instituteId: instituteId,
                is_leaving: "leaving",
                staffId: getQuery.state?.sid,
              },
            });
          } else if (type === "MTC") {
            navigate(`/q/${params.username}/student/certificate/form`, {
              state: {
                student: res.data.student,
                download: res.data.download,
                instituteId: instituteId,
                is_leaving: "MTC",
                staffId: getQuery.state?.sid,
              },
            });
          } else if (type === "MLC") {
            navigate(`/q/${params.username}/student/certificate/form`, {
              state: {
                student: res.data.student,
                download: res.data.download,
                instituteId: instituteId,
                is_leaving: "MLC",
                staffId: getQuery.state?.sid,
              },
            });
          } else {
            navigate(`/q/${params.username}/student/certificate/form`, {
              state: {
                student: res.data.student,
                download: res.data.download,
                instituteId: instituteId,
                is_leaving: "transfer",
                staffId: getQuery.state?.sid,
              },
            });
          }
        })
        .catch({});
    }
  };

  const onChoose = (vt) => {
    setStep(1);
    setState(vt);
  };
  const onSelectSingleStudent = (val) => {
    if (state === "bonafide") {
      navigate(`/q/${params.username}/student/certificate/form`, {
        state: {
          studentId: val?._id,
          staffId: getQuery.state?.sid,
          is_bonafide: true,
          instituteId: instituteId,
        },
      });
    } else {
      onNavigate(state, val);
    }
  };

  return (
    <>
      {step === 1 ? (
        <InstituteStudent
          title={t("select_student")}
          onClose={onCloseStep}
          instituteId={instituteId}
          onSelectStudent={onSelectSingleStudent}
          isSearchOnly
        />
      ) : (
        <ModalMenu
          onClose={onClose}
          customStyle={{
            width: "22rem",
          }}
        >
          <h6 onClick={() => onChoose("bonafide")}>{t("bonafide")}</h6>
          <h6 onClick={() => onChoose("leaving")}>{t("leaving")}</h6>
          <h6 onClick={() => onChoose("MTC")}>{t("transfer_certificate")}</h6>
          <h6 onClick={() => onChoose("MLC")}>{t("migration_tc")}</h6>
          <h6 onClick={() => onChoose("transfer")}>{t("migration_lc")}</h6>
        </ModalMenu>
      )}

      {disabled && <QvipleLoader />}
    </>
  );
};

export default CertificateIssueMenu;
